.CustomSelectOption {
  width: 100%;
  display: flex;
  align-items: center;
  position: relative;
  box-sizing: border-box;
  padding: 8px 12px;
  white-space: nowrap;
  user-select: none;
  cursor: pointer;
  color: var(--text_primary, var(--vkui--color_text_primary));
}

.CustomSelectOption--hierarchy {
  --custom-select-option-hierarchy-level: 0;

  padding-left: calc(
    var(--custom-select-option-hierarchy-level) *
      var(--vkui--size_option_hierarchy--regular)
  );
}

.CustomSelectOption--sizeY-regular {
  min-height: 44px;
}

.CustomSelectOption--disabled {
  cursor: default;
  opacity: var(--vkui--opacity_disable_accessibility);
}

.CustomSelectOption--hover {
  background-color: var(
    --button_muted_background,
    var(--vkui--color_transparent--hover)
  );
}

.CustomSelectOption__before {
  flex-shrink: 0;
  margin-right: 7px;
}

.CustomSelectOption__main {
  flex-grow: 1;
  max-width: 100%;
  min-width: 0;
}

.CustomSelectOption__children {
  min-width: 0;
  overflow: hidden;
  text-overflow: ellipsis;
}

.CustomSelectOption__after {
  margin-left: 8px;
  flex-shrink: 0;
  display: flex;
  align-items: center;
}

.CustomSelectOption__description {
  color: var(--text_secondary, var(--vkui--color_text_secondary));
  text-overflow: ellipsis;
  overflow: hidden;
}

.CustomSelectOption__selectedIcon {
  color: var(--accent, var(--vkui--color_icon_accent));
}

.CustomSelectOption__selectedIcon:not(:first-child) {
  margin-left: 8px;
}
