.CellButton {
  box-sizing: border-box;
  text-decoration: none;
  margin: 0;
  border: none;
  cursor: pointer;
  width: 100%;
  background: transparent;
  text-align: left;
  padding-top: 0;
  padding-bottom: 0;
}

.FormItem .CellButton {
  width: 100%;
  box-sizing: content-box;
  margin: 0 calc(-1 * var(--vkui--size_base_padding_horizontal--regular));
}

.CellButton[disabled] {
  opacity: 0.4;
}

.CellButton--centered {
  justify-content: center;
  text-align: center;
}

.CellButton--centered .SimpleCell__main {
  flex-grow: initial;
  max-width: initial;
}

.CellButton--centered > .Icon {
  padding-right: 8px;
}

.CellButton {
  color: var(--accent, var(--vkui--color_text_accent));
}

.CellButton > .Icon {
  color: var(--accent, var(--vkui--color_icon_accent));
}

.CellButton--danger {
  color: var(--destructive, var(--vkui--color_text_negative));
}

.CellButton--danger > .Icon {
  color: var(--destructive, var(--vkui--color_icon_negative));
}
